body {
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "GothamPro_Bold";
  src: url("../fonts/GothamPro_Bold.woff2") format("woff2"), url("../fonts/GothamPro_Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "GothamPro_Medium";
  src: url("../fonts/GothamPro_Medium.woff2") format("woff2"), url("../fonts/GothamPro_Medium.woff") format("woff");
  font-weight: medium;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "GothamPro_Regular";
  src: url("../fonts/GothamPro_Regular.woff2") format("woff2"), url("../fonts/GothamPro_Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "MuseoSansCyrl_500";
  src: url("../fonts/MuseoSansCyrl_500.woff2") format("woff2"), url("../fonts/MuseoSansCyrl_500.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "MuseoSansCyrl_700";
  src: url("../fonts/MuseoSansCyrl_700.woff2") format("woff2"), url("../fonts/MuseoSansCyrl_700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
container,
.header__wrapper,
.intro__wrapper,
.footer__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 150px;
}
@media (max-width: 1199px) {
  container,
  .header__wrapper,
  .intro__wrapper,
  .footer__wrapper {
    max-width: 1199px;
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  container,
  .header__wrapper,
  .intro__wrapper,
  .footer__wrapper {
    max-width: 767px;
    padding: 0 20px;
  }
}
@media (max-width: 1199px) {
  .logo {
    width: 82px;
    height: 18px;
  }
}
@media (max-width: 1199px) and (max-width: 767px) {
  .logo {
    width: 52px;
    height: 11px;
  }
}
.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 56px;
}
.header__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  list-style: none;
  margin: 0;
  padding: 0;
  flex-grow: 1;
}
.header__item {
  width: 155px;
  margin-right: 30px;
}
.header__item:nth-child(3n) {
  margin-right: 0;
  padding-right: 0;
}
.header__item-icon {
  fill: #8391b2;
}
.header__item-icon--phone {
  margin-right: 4px;
}
.header__item-icon--mail {
  margin-right: 10px;
}
.header__item-icon--tg {
  margin-right: 8px;
}
.header__link {
  font-family: GothamPro_Regular;
  font-size: 12px;
  line-height: 20px;
  display: flex;
  align-items: center;
  text-decoration: none;
  letter-spacing: 0.0025em;
  color: #8391b2;
}
@media (max-width: 1199px) {
  .header__item {
    width: auto;
    margin-right: 38px;
  }
  .header__item span {
    display: none;
  }
  .header__item svg {
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    min-height: 32px;
    padding: 0 20px;
  }
  .header__item {
    margin-right: 16px;
  }
  .header__item-icon--phone {
    width: 8px;
    height: 8.67px;
  }
  .header__item-icon--mail {
    width: 9px;
    height: 6.75px;
  }
  .header__item-icon--tg {
    width: 8px;
    height: 8px;
  }
}
.intro__wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 25px 110px 0 150px;
}
.intro__content {
  max-width: 560px;
  padding-top: 52px;
}
.intro__title {
  font-family: GothamPro_Bold;
  font-size: 32px;
  line-height: 38px;
  letter-spacing: 0.0025em;
  margin: 0;
  margin-bottom: 18px;
  color: #29435d;
}
.intro__description {
  font-family: GothamPro_Regular;
  font-size: 18px;
  line-height: 22px;
  padding: 0;
  margin: 0;
  margin-bottom: 25px;
  letter-spacing: 0.0025em;
  color: #8391b2;
}
.intro__btn {
  display: block;
  font-family: MuseoSansCyrl_500;
  font-size: 14px;
  line-height: 24px;
  width: 160px;
  min-height: 32px;
  background: #2f93fe;
  border-radius: 6px;
  text-decoration: none;
  letter-spacing: 0.045em;
  color: #fff;
  padding: 4px 5px 0 20px;
}
@media (max-width: 1199px) {
  .intro__wrapper {
    padding: 33px 35px 0 30px;
  }
  .intro__content {
    width: 50%;
    padding-top: 27px;
    flex-grow: 1;
  }
  .intro__title {
    font-size: 26px;
    line-height: 31px;
  }
  .intro__description {
    font-size: 16px;
    line-height: 19px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper {
    position: relative;
    padding: 0 20px 150px;
  }
  .intro__wrapper img {
    position: absolute;
    bottom: 0;
    right: 28px;
  }
  .intro__content {
    padding-top: 31px;
  }
  .intro__title {
    font-size: 18px;
    line-height: 22px;
  }
  .intro__description {
    font-size: 12px;
    line-height: 14px;
  }
  .intro__btn {
    width: 111px;
    min-height: 15px;
    font-size: 10px;
    padding: 0;
    padding-left: 13px;
  }
}
.footer {
  margin-top: auto;
  box-shadow: 0px -1px 5px rgba(0,0,0,0.1);
}
.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  min-height: 172px;
  padding-top: 38px;
}
.footer__logo {
  margin-bottom: 26px;
}
.footer__social {
  width: 200px;
  padding-top: 3px;
  padding-left: 4px;
}
.footer__nav {
  width: 50%;
  flex-grow: 1;
}
@media (max-width: 1199px) {
  .footer__wrapper {
    min-height: 163px;
    padding-top: 27px;
  }
  .footer__social {
    width: 148px;
    padding-top: 5px;
    padding-left: 0;
  }
  .footer__logo {
    margin-bottom: 21px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    padding-right: 0;
    padding-top: 21px;
  }
  .footer__social {
    width: 52px;
    margin-right: 28px;
  }
  .footer__logo {
    margin-bottom: 16px;
  }
}
.social__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}
.social__item {
  margin-right: 20px;
}
@media (max-width: 1199px) {
  .social__list {
    padding-left: 3px;
  }
  .social__item {
    margin-right: 16px;
  }
  .social__icon {
    width: 16px;
    height: 16px;
  }
}
@media (max-width: 767px) {
  .social__list {
    padding: 0;
  }
  .social__item {
    padding-left: 3px;
    margin-right: 7px;
    margin-bottom: 10px;
  }
  .social__item--fb {
    order: 2;
  }
  .social__item--insta {
    order: 1;
  }
}
.nav__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav__item {
  width: 140px;
  margin-right: 46px;
  margin-bottom: 23px;
}
.nav__item:nth-child(4n) {
  margin-right: 0;
}
.nav__link {
  font-family: MuseoSansCyrl_500;
  font-size: 14px;
  line-height: 17px;
  text-decoration: none;
  letter-spacing: 0.0275em;
  color: #8391b2;
}
@media (max-width: 1199px) {
  .nav__item {
    width: 115px;
    margin-right: 32px;
    margin-bottom: 16px;
  }
  .nav__link {
    font-size: 12px;
    line-height: 14px;
  }
}
@media (max-width: 767px) {
  .nav__item {
    width: 110px;
    margin-right: 0;
    margin-bottom: 9px;
  }
  .nav__item:nth-child(4n) {
    margin-right: 0;
  }
  .nav__item--host {
    order: 1;
  }
  .nav__item--equipment {
    order: 7;
  }
  .nav__item--data {
    order: 4;
  }
  .nav__item--blog {
    order: 2;
  }
  .nav__item--cloud {
    order: 5;
  }
  .nav__item--decision {
    order: 3;
  }
  .nav__item--company {
    order: 8;
  }
  .nav__item--office {
    order: 6;
  }
}
